<template lang="pug">
div.weibo-page(ref="weiboPage")
  weibo-chat-item.animate__animated.animate__fadeInUp(
    v-for="(item, index) in messages",
    :key="index",
    :type="item.type",
    :text-content="item.text",
    :src="item.src"
  )
</template>

<script>
import WeiboChatItem from '@/components/WeiboChatItem'
import sleep from '@/utils/sleep'
// import { disableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock'
import { clearAllBodyScrollLocks } from 'body-scroll-lock'

const messages = [
  {
    type: 'text',
    text: '有人在Twitter上发了你的照片。'
  },
  {
    type: 'image',
    src: require('@/assets/twitter-in-weibo.png')
  }
]

export default {
  name: 'WeiboPage',
  components: {
    WeiboChatItem
  },
  props: {
    isActive: {
      type: Boolean,
      default: false
    },
    msg: String
  },
  data () {
    return {
      messages: []
    }
  },
  mounted () {
    messages.forEach(async (message, index) => {
      await sleep(1000 * (index + 1))
      this.messages.push(message)
      if (index === messages.length - 1) {
        clearAllBodyScrollLocks()
      }
    })
  },
  beforeDestroy () {
    this.messages = []
    clearAllBodyScrollLocks()
  }
}
</script>

<style scoped lang="scss">
.weibo-page {
  color: #FFF;
}
</style>
