<script>
export default {
  name: 'MobileMockup',
  props: {
    appName: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isShowScrollTips: false,
      curTipKey: 1,
      tips: {
        1: '可向下滑動',
        2: '在手機屏幕內滑動<br/>可瀏覽前面訊息'
      }
    }
  },
  methods: {
    showScrollTips (key) {
      this.curTipKey = key
      this.isShowScrollTips = true
      setTimeout(() => {
        this.isShowScrollTips = false
      }, 4000)
    }
  },
  mounted () {
    this.$bus.on('showScrollTips', this.showScrollTips)
  }
}
