<template lang="pug">
div.your-choose
  p.title
    span(ref="title")
  div.content(:class="{'active': isTyped}")
    p(
      v-for="(content, index) in contents[option].content",
      :key="index"
    ) {{ content }}
</template>

<script>
import Typed from 'typed.js'

export default {
  name: 'YourChoose',
  props: {
    option: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      isTyped: false,
      contents: {
        a: {
          title: '我到了家附近的派出所報警。',
          content: [
            '小Q得到了治安拘留五天的處罰。',
            '可是不久後我發現，他在拘留結束後依然肆無忌憚地做著那些事。'
          ]
        },
        b: {
          title: '我繼續追查自己照片流傳到哪些地方。',
          content: [
            '從twitter、Telegram，翻查到一些色情網站。',
            '我每天花費四個多小時在不同平台上尋找自己照片，也無意中發現照片背後的複雜傳播鏈和產業鏈……'
          ]
        },
        c: {
          title: '我自己撰寫文章，在網絡上發帖曝光這些色情群組的行為。',
          content: [
            '這篇文章被許多關注女性權益的人轉發，引起社會關注和輿論熱議。',
            '但此時，Telegram群組裏的人也在討論這件事。',
            '我被人肉了。'
          ]
        },
        d: {
          title: '我將此事報料給媒體，並匿名接受記者採訪。',
          content: [
            '報道引發社會關注和輿論熱議，不過色情群組卻更熱鬧了……'
          ]
        }
      }
    }
  },
  methods: {
    typed () {
      const _this = this
      this.typed = new Typed(this.$refs.title, {
        strings: [this.contents[this.option].title],
        smartBackspace: true,
        backDelay: 2000,
        startDelay: 500,
        typeSpeed: 50,
        showCursor: true,
        cursorChar: '_',
        autoInsertCss: true,
        onComplete () {
          _this.isTyped = true
        }
      })
    }
  },
  mounted () {
    this.typed()
  }
}
</script>

<style scoped lang="scss">
.your-choose {
  font-size: 20px;
  color: #FFF;
  max-width: 80vw;
  .title {
    color: #1F87FD;
    margin-bottom: 30px;
  }
  .content {
    opacity: 0;
    transition: all 1s ease 0.5s;
    &.active {
      opacity: 1;
    }
  }
}
</style>
